/********** Template CSS **********/
:root {
  --primary: #2124b1;
  --secondary: #4777f5;
  --light: #f7faff;
  --dark: #1d1d27;

  --main-Black-S: #313131;
  --main-Gray-S: #606060;
  --main-Gold-S: #f1d593;
  --white: #fff;
  --background-Black-S: #222;

  /*color texts*/
  --body-Text-S: #313131;
  --light-Gray-S: #e0e0e0;
  --lines-S: #d8d8d8;
}
.headingRecife-Parent .h1-font-size, .headingRecife-Parent .h2-font-size, .headingRecife-Parent .h3-font-size, .headingRecife {
  font-family: RecifeDisplay-Light;
  font-weight: 300;
}
.darkFont, .darkFont h1, .darkFont h2, .darkFont h3, .darkFont h4, .darkFont h5, .darkFont h6, .darkFont li, .darkFont span, .darkFont p {
  color: var(--body-Text-S);
}
.lightFont, .lightFont h1, .lightFont h2, .lightFont h3, .lightFont h4, .lightFont h5, .lightFont h6, .lightFont p, .lightFont li {
  color: var(--light-Gray-S);
}
.goldenLink, p.goldenLink, a.goldenLink, .goldenLink p, .goldenLink a, h4.goldenLink , h5.goldenLink , h6.goldenLink  {
  color: var(--main-Gold-S);
  text-decoration: none;
}
.headingRecife-Parent .h1-font-size, .headingRecife-Parent .h2-font-size, .headingRecife-Parent .h3-font-size, .headingRecife {
  font-family: RecifeDisplay-Light;
}
img {
  border-style: none;
}

h1, h2, h3, h4, h5, h6 {

  line-height: 100%;
}
.link-no-underline {
    text-decoration: none;
    color: #219963;
}
h1 a, h2 a, h3 a, h4 a, h5 a, h6 a, strong a {

  text-decoration: none;
}
.overflowHidden {
  overflow: hidden;
}

.fontWeight700, .fontWeight700 h1, .fontWeight700 h2, .fontWeight700 h3, .fontWeight700 h4, .fontWeight700 h5, .fontWeight700 h6, .fontWeight700 p {
  font-weight: 700;
}
.fontWeight500, .fontWeight500 h1, .fontWeight500 h2, .fontWeight500 h3, .fontWeight500 h4, .fontWeight500 h5, .fontWeight500 h6, .fontWeight500 p {
  font-weight: 500;
}
.fontWeight400, .fontWeight400 h1, .fontWeight400 h2, .fontWeight400 h3, .fontWeight400 h4, .fontWeight400 h5, .fontWeight400 h6, .fontWeight400 p{
  font-weight: 400;
}
.fontWeight300, .fontWeight300 h1, .fontWeight300 h2, .fontWeight300 h3, .fontWeight300 h4, .fontWeight300 h5, .fontWeight300 h6, .fontWeight300 p{
  font-weight: 300;
  letter-spacing: .8px;
}

.font-weight200 {
  font-weight: 200;
}
.spacingletters-for-thin p {
  letter-spacing: 0.75px;
}


/*background divs*/
.darkBackground {
  background-color: var(--background-Black-S);
  overflow: hidden;

  .lightFont, .lightFont h1, .lightFont h2, .lightFont h3, .lightFont h4, .lightFont h5, .lightFont h6, .lightFont p, .lightFont li {
    color: var(--light-Gray-S);
  }
}
.lightBackground {
  background-color: #fff;
  overflow: hidden;

  .lightFont, .lightFont h1, .lightFont h2, .lightFont h3, .lightFont h4, .lightFont h5, .lightFont h6, .lightFont p, .lightFont li {
    color: var(--body-Text-S);
  }
}

