.buttonsWrap {
    display: flex;
    flex-wrap: wrap;
    column-gap: 1%;
    row-gap: 1vh;
    align-items: center;
    width: 100%;

    &.buttonsWrapCenter {
      justify-content: center;
    }
}
.main-footer .buttonsWrap {
  column-gap: 4vh;
}
.sButton {
    padding: 10px;
    font-size: 18px;
    text-decoration: none;
    display: flex;
    align-items: center;
    column-gap: 10px;
    opacity: 1;
    background-color: transparent;
    border: 0;
    border-radius: 0;
    font-weight: 300;
    letter-spacing: 2px;
    transition: all 0.4s ease-out;

    img {
        width: 7px;
    }
    & + img {
      width: 7px;
    }
    span {
        margin-top: 5px;
    }
    &:hover {
      opacity: .7;
    }
    &.sButton-fullwidth {
      width:100%;
      &.sButton-right {
          justify-content: flex-end;
          border-bottom: 1px solid;
      }
    }
}


form {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding: 0px;
    gap: 10px;
    isolation: isolate;

    input, textarea {
      display: flex;
      flex-direction: row;
      align-items: flex-start;
      padding: 20px;
      gap: 10px;
      /* width: 560px; */
      /* height: 72px; */
      background: #0F0F0F;
      border: 1px solid #3E3E3E;
      border-radius: 4px;
    } 
}
form button, .formButton {
    border: 1px solid var(--lines-S);
    padding: 20px 40px;
    font-size: 24px;
    background-color: transparent;
    border-radius: 7px;
    color: var(--light-Gray-S);
    text-decoration: none;

    &:hover {
      border-color: #fff;
    }
    span {
      margin-bottom: 3px;
      display: block;
      font-size: 18px;

    }
}

.buttonsWrap.buttonsWrapFooter{
  flex-wrap: nowrap;
  column-gap: 0;

  .sButton.sButton-fullwidth.sButton-right {
    border-bottom: 0;
  }
}

.checkboxContainer, .radioboxContainer {
    margin: 1rem 0;
    display: flex;
    align-items: center;
    span {
      margin-left: 1rem;
      font-weight: 500;
    }
}
.check-input, .radio-input {
    display: none;
}
.checkbox {
  border-radius: 4px;
}
.radiobox {
  border-radius: 50%;
  &:before {
    border-radius: 50%;
  }
}
.checkbox, .radiobox {
  padding: 3px;
    width: 20px;
    height: 20px;
    border: 1px solid;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    position: relative;
    transition: 0.3s all;

    &:before {
      content: "";
      background: #fff0;
      position: absolute;
      border-radius: 3px;
      display: block;
      width: 16px;
      height: 16px;
      transition: all 0.4s ease-out;
    }
}
.check-input:checked + .checkbox:before, .radio-input:checked + .radiobox:before {
    background: #fff;
}

/***Hero***/
.lightBackground {
  background-color: #fff;

  .breadcrumbs-parent .breadcrumbs a {
      color: var(--body-Text-S);
  }
}
.darkBackground  {
  background-color: var(--background-Black-S);

  .breadcrumbs-parent .breadcrumbs a {
      color: var(--light-Gray-S);
  }
}
.heavyDarkBackground {
  background-color: #141414;

  .breadcrumbs-parent .breadcrumbs a {
      color: var(--light-Gray-S);
  }
}
.breadcrumbs-parent {
    padding: 14px 20px;
    flex: 1 0 100%;
    max-height: 50px;
    position: absolute;
    left: 0;
    top: 0;

    .breadcrumbs {
        display: flex;
        align-items: center;
        margin: 0;
        padding: 0;
        column-gap: 10px;
        list-style: none;
    }
}


.container-1024 {
  margin: 0 auto;
  max-width: 1024px;
}
.container-640 {
  margin: 0 auto;
  max-width: 640px;
}
section.hero-Parent {
  width: 100%;
  position:relative;
  

  .hero-Flex {
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
    min-height: 80vh;
  }
}

/***Main Heading***/
.main-heading-parent {
    overflow: hidden;

    .main-heading-inner {
        margin: 130px 1vh 30px;
    }
}


/***Image content - section***/
.sFlex-wrapper {
    margin: 0 auto;
    max-width: 90%;

    &.sFlex-parent {
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        column-gap: 8%;
        position: relative;
        z-index: 1;

        @include media-breakpoint-up(md750){
          flex-direction: row;

          &.sFlex-reverse {
            flex-direction: row-reverse;

            .maxwidth-shortcontent {
              margin-left: auto;
            }
          }
        }

        &.sFlex-start {
          align-items: flex-start;
        }
        &.sFlex-end {
          align-items: flex-end;
        }
        .sFlexImg.sFlex-1-3 {
            position: relative;
            flex: 1 0 40%;
            display: flex;
            justify-content: center;

            .subImg {
                position: absolute;
                object-fit: cover;
                object-position: center;

                &.top-left {
                  left: -100px;
                  top: -95px;
                  max-width: 60%;
                }
                &.bottom-left {
                  left: -100px;
                  bottom: -97px;
                  max-width: 60%;
                }
                &.bottom-right {
                  right: -100px;
                  bottom: -97px;
                  max-width: 60%;
                }
                &.top-right {
                  right: -100px;
                  top: -95px;
                  max-width: 60%;
                }
                &.frontShow {
                  z-index: 1;
                }
            }
            .mainImg {
                max-width: 630px;
                position: relative;
                width: 85%;
            }
        }
        .sFlex-bottom-parent {
            margin-bottom: 95px;
        }
        .sFlex-top-parent {
            margin-top: 175px;
        }
        .sFlexContent {
            margin-top: 20px;

            &.sFlex-2-3 {
                flex: 1 0 50%;
            }
            .maxwidth-shortcontent {
                max-width: 610px;
            }
            .sFlexContent-inner {
                padding: 30px 0;
                position: relative;
                display: flex;
                flex-direction: column;

                & > * {
                  margin-bottom: 20px;
                }
                p {
                  font-size: 18px;
                }
            }
        }
        &.showBigFirstHeading {

            & .sFlexContent-inner > * {
                order: 10;
            }
            .h2-font-size {
                order: 1;
            }
            .h4-font-size {
                order: 2;
            }
        }
        &.showSmallFirstHeading {

            & .sFlexContent-inner > * {
                order: 10;
            }
            .h2-font-size {
                order: 2;
            }
            .h4-font-size {
                order: 1;
            }
        }
    }
}


