.center-images {
    position: relative;

    background: var(--gradient);

    margin-top: var(--margins-top--mobile);
    margin-bottom: var(--margins-bottom--mobile);
    padding-top: var(--paddings-top--mobile);
    padding-bottom: var(--paddings-bottom--mobile);

    @media (min-width: 767.98px) {
        margin-top: var(--margins-top);
        margin-bottom: var(--margins-bottom);
        padding-top: var(--padding-top);
        padding-bottom: var(--paddings-bottom);
    }

    img {
        border-radius: 8px;
        max-width: 100%;

    }

    .first_image {
        margin: 0 auto;
        z-index: 2;
        position: relative;
        max-width: 80%;

        @media (min-width: 767.98px) {
            max-width: 600px;

           img{
            transform: translate(10%, 0px);
           }

        }
    }

    .second_image {
        position: absolute;
        bottom: 0;
        left: 0;
        right: 0;
        z-index: 1;
        max-width: 50%;
        margin: 20px;

        @media (min-width: 767.98px) {
            margin: 0 auto;
            max-width: 300px;

            img{
                transform: translate(-70%, -30%);
            }
        }
    }

    .container {
        position: relative;
        padding: 100px 0;

        @media (min-width: 767.98px) {
            min-height: 900px;
            padding: 200px 0;


        }
    }

}